import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
  CButton,
  CButtonGroup,
  CFormSelect,
} from '@coreui/react'


import moment from 'moment'

import Loader from '../../components/Loader'
import Message from '../../components/Message'
import { Authentification } from '../../components/index'

import {
  // getOrders,
  getChiffreAffaires,
  // getDashboard,
} from '../../provider/dashboardFetch'

const Historiques = () => {
  /** State */
  const [chiffreAffaires, setChiffreAffaires] = useState(null)
  const [data, setData] = useState(null)
  const [error, setError] = useState(null)
  const [type, setType] = useState('Day')
  const [status, setStatus] = useState('READING')

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log('Run once, when the component mounts')

    async function fetch() {
      const resp_1 = await getChiffreAffaires()

      resp_1?.status
        ? setChiffreAffaires({ ...resp_1?.data })
        : setError(resp_1?.error)
    }

    fetch()
  }, [])

  useEffect(() => {
    console.log('Dependency')
    let dataTMP = []
    let dataGlobal = []

    if (type === 'Day') {
      if (chiffreAffaires?.ordersChiffresGroupedByDayStatus?.length)
        dataGlobal = [...chiffreAffaires?.ordersChiffresGroupedByDayStatus]
    } else if (type === 'Month') {
      if (chiffreAffaires?.ordersChiffresGroupedByMonthWithStatus?.length)
        dataGlobal = [
          ...chiffreAffaires?.ordersChiffresGroupedByMonthWithStatus,
        ]
    } else if (type === 'Year') {
      if (chiffreAffaires?.ordersChiffresGroupedByYearWithStatus?.length)
        dataGlobal = [...chiffreAffaires?.ordersChiffresGroupedByYearWithStatus]
    }

    if (dataGlobal?.length) {
      for (const order of dataGlobal) {
        if (status === order.status) {
          if (type === 'Day') {
            dataTMP = [...dataTMP, {...order, updatedAt: moment(order.updatedAt).format('DD/MM/YYYY')}]
          } else if (type === 'Month') {
            dataTMP = [...dataTMP, {...order, updatedAt: moment(order.updatedAt).format('MM/YYYY')}]
          } else if (type === 'Year') {
            dataTMP = [...dataTMP, {...order, updatedAt: moment(order.updatedAt).format('YYYY')}]
          }
        }
      }

      setData([...dataTMP])
    }
  }, [status, type, chiffreAffaires])

  const toggleType = (value, key) => {
    if (key === 'status') setStatus(value)
    else if (key === 'type') setType(value)
  }

  if (chiffreAffaires === null) {
    return <Loader />
  }

  if (error) {
    return <Message> {error}</Message>
  }

  return (
    <CRow>
      <Authentification />
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Listes des Historiques</strong>
              </CCol>
              <CCol sm={7} className="d-none d-md-block">
                <CFormSelect
                  value={status}
                  aria-label="Status"
                  className="float-end mb-1"
                  onChange={(e) => {
                    toggleType(e.target.value, 'status')
                  }}
                >
                  {[
                    'ORDER_STATUS_UNSPECIFIED',
                    'CONFIRMED',
                    'PENDING',
                    'PENDING_MERCHANT_CONFIRMATION',
                    'CANCELED',
                    'FAILED',
                    'DECLINED_BY_MERCHANT',
                    'READING',
                  ].map((value) => (
                    <option value={value} key={value}>
                      {value}
                    </option>
                  ))}
                </CFormSelect>
                <CButtonGroup className="float-end me-3">
                  {['Day', 'Month', 'Year'].map((value) => (
                    <CButton
                      onClick={() => toggleType(value, 'type')}
                      color="outline-secondary"
                      key={value}
                      className="mx-0"
                      active={value === type}
                    >
                      {value}
                    </CButton>
                  ))}
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CTable>
              <CTableHead>
                <CTableRow>
                  <CTableHeaderCell scope="col">Total</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Status</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Date</CTableHeaderCell>
                </CTableRow>
              </CTableHead>
              <CTableBody>
                {data && data.length > 0 ? (
                  data.map((d, index) => (
                    <CTableRow key={index}>
                      <CTableDataCell>{d.total.toLocaleString("fr-FR")} $</CTableDataCell>
                      <CTableDataCell>{d.status}</CTableDataCell>
                      <CTableDataCell>{ d.updatedAt}</CTableDataCell>
                    </CTableRow>
                  ))
                ) : (
                  <h3>{'NO Items'}</h3>
                )}
              </CTableBody>
            </CTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )
}

export default Historiques
